import { useLocation, useParams, useSearchParams } from "react-router-dom";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import FinanceAllStudent from "../../FinanceManager/FinanceAllStudent/FinanceAllStudent";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import AdmissionCancelledList from "./AdmissionCancelledList";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/FinanceManager.module.css";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";

const AdmissionCancelled = ({ instituteId, studentAlias, aid }) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const params = useParams();
  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("cancel_admission")}</h6>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("n"))}
            parentState={getQuery.state}
            url={
              getQuery.state?.urlOffSet === "Institute"
                ? `/q/${params.username}/institute/member/admission?type=cancel-admission${tab.urlKey}`
                : `/q/${params.username}/member/staff/admission?type=cancel-admission${tab.urlKey}`
            }
          />
        ))}
      </HorizontalTabWrapper>
      <BorderBottom
        customStyleBorder={{
          marginTop: "0",
        }}
      />
      {!searchParams.get("n") ? (
        <FinanceAllStudent
          instituteId={instituteId}
          studentAlias={studentAlias}
          invalidateAccess="unAuthorized"
          allStudentControl={allStudentControl}
        />
      ) : searchParams.get("n") === "cancelled" ? (
        <UserMemberContentWrapper>
          <AdmissionCancelledList aid={aid} instituteId={instituteId} />
        </UserMemberContentWrapper>
      ) : null}
    </UserMemberContainerWrapper>
  );
};

export default AdmissionCancelled;

const allStudentControl = {
  collectAccess: "Other",
  govtAccess: "Other",
  cancelAccess: true,
  editStructureControl: "authorized",
  editGovtNumberContorl: "unauthorized",
  editInstallmentControl: "unauthorized",
  editGovtInstallmentControl: "unauthorized",
};

const tabList = [
  {
    label: "all_students",
    id: "student",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "cancelled",
    id: "cancelled",
    urlKey: "&n=cancelled",
    groupIs: ["cancelled"],
  },
];
