import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { useAdmissionApplicantCancelledList } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import style from "../../FinanceManager/FinanceManager.module.css";
import AdmissionCancelledCard from "../OneOngoingAdmission/AdmissionCancelled/AdmissionCancelledCard";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../Ui/Button/CreateButton";
import CancelFeeHeadFilter from "../../../../MembersTab/staff/Mainbody/FinanceManager/Filter/FeesHead/CancelFeeHeadFilter";
const AdmissionCancelledList = ({ aid, instituteId }) => {
  const { t } = useTranslation();

  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [isExport, setIsExport] = useState(false);

  const {
    admissionApplicantCancelledList,
    admissionApplicantCancelledListLoading,
  } = useAdmissionApplicantCancelledList({
    data: {
      aid: aid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !aid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionApplicantCancelledList?.list?.length) {
      let obj = nextPage(admissionApplicantCancelledList?.list?.length);
      setState(() => obj);
    }
  }, [admissionApplicantCancelledList?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onExport = () => {
    setIsExport((pre) => !pre);
  };

  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBottom: "0.7rem",
        }}
      >
        <section className={style.fm_search_full_container}>
          <div className={style.fm_search_full_container_inner}>
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
              name="search"
            />
          </div>
        </section>
        <CreateButton
          label="export"
          customStyle={{
            paddingBlock: "0.4rem",
            margin: "0",
          }}
          onAction={onExport}
        />
      </div>
      {admissionApplicantCancelledList?.list?.map((applicant, index) =>
        admissionApplicantCancelledList?.list?.length === index + 1 ? (
          <div key={applicant?._id} ref={ref}>
            <AdmissionCancelledCard
              applicant={applicant}
              instituteId={instituteId}
            />
          </div>
        ) : (
          <AdmissionCancelledCard
            applicant={applicant}
            key={applicant?._id}
            instituteId={instituteId}
          />
        )
      )}
      {admissionApplicantCancelledListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        admissionApplicantCancelledList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No admission cancelled found related this search."
          />
        )
      ) : (
        admissionApplicantCancelledList?.list?.length === 0 && (
          <EmptyMessage
            customStyleContainer={{
              marginTop: "1.5rem",
            }}
            title="No admission cancelled list."
          />
        )
      )}
      {isExport && (
        <CancelFeeHeadFilter
          onClose={onExport}
          aid={aid}
          instituteId={instituteId}
        />
      )}
    </>
  );
};

export default AdmissionCancelledList;
